
 *{

  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}
.about{
  color: rgb(238, 229, 225);
font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

}

.team{
  overflow: hidden;
background-repeat: no-repeat;
background-size: cover;
width: 100%;
height: 100vh;
 background-image:url("https://images.unsplash.com/photo-1531297484001-80022131f5a1?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8cG9ydGZvbGlvJTIwYmFja2dyb3VuZHxlbnwwfHwwfHw%3D&auto=format&fit=crop&w=500&q=60");
}



.parent{
  /* background-color: whitesmoke; */
display: flex;
justify-content: space-around;
margin-top: 5rem;
font-size: 3rem;
font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
margin-left:5rem;
}
.span1{
  color: rgb(238, 229, 225);
font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

  
}
.mail{
  display: flex;
  align-items: center;
  
  /* justify-content: center; */
}
.resume{
margin: 25px;
margin-left: 18px;
font-size: 0.1px;
}
.img{
  width:40vw ;
  height: 80vh;
  margin-left: 5rem;
}

.Homebtn{
  width: 6vw;
  height: 6vh;
  font-size:1.1rem;
  border-radius: 1rem;
  background-color: rgb(56, 12, 30);
color:rgb(230, 210, 210);
/* border-color: rgb(15, 15, 15); */
box-shadow: 0px 0px 0px 2px  rgb(247, 242, 242);
cursor: pointer;
text-transform: uppercase;

}
.Homebtn:hover{
  background-color: green;
}
::-webkit-scrollbar{
  display: none;
}

@media only screen and (max-width: 600px) {
.img{
  width:100vw ;
 height: 30vh;

} 
.about{
  font-size: 2.58rem;
}
.Homebtn{
  width:18vw;
  height:8vh;
  font-size: 0.75rem;
  font-weight: bold;
}

}